<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <v-card color="basil">
        <v-card-title class="text-center justify-center py-6">
          <h1 class="font-weight-bold display-3 black--text">
            {{ $t('菜品及销量统计') }}
          </h1>
        </v-card-title>
        <v-card-text>
          <v-row>
            <v-col cols="6">
              <v-select
                v-model="fixedHideCategories"
                :items="categoryList"
                item-text="_langsname"
                item-value="id"
                :label="$t('永久隐藏类型')"
                append-icon="mdi-close"
                multiple
                @click:append="clearFixedCategory"
                @change="selectFixedCategory(fixedHideCategories)"
              />
            </v-col>
            <v-col cols="6">
              <v-select
                v-model="fixedHideAttributes"
                :items="realAttributes"
                :label="$t('永久隐藏属性')"
                item-text="name"
                item-value="id"
                append-icon="mdi-close"
                multiple
                @click:append="clearFixedHideAttribute"
                @change="selectAttributes(fixedHideAttributes)"
              />
            </v-col>
          </v-row>
        </v-card-text>
        <v-tabs
          background-color="transparent"
          color="primary"
          grow
        >
          <v-tab
            v-for="(tab,i) in tabs"
            :key="i"
          >
            {{ tab }}
          </v-tab>
          <v-tab-item>
            <ik-data-table
              ref="statisticTable"
              :model="model"
              :use-action="false"
              :use-select="false"
              icon="mdi-calculator-variant"
              :entity-name="$t('菜品统计')"
              :filter="filter"
            >
              <template v-slot:filterLeft>
                <v-menu
                  ref="menu"
                  v-model="menu"
                  :close-on-content-click="false"
                  :return-value.sync="dates"
                  offset-y
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="dates"
                      class="ml-auto"
                      hide-details
                      :label="$t('日期筛选')"
                      prepend-icon="mdi-calendar"
                      readonly
                      style="max-width: 250px;"
                      single-line
                      append-icon="mdi-close"
                      @click:append="clear"
                      v-on="on"
                    />
                  </template>
                  <v-date-picker
                    v-model="dates"
                    no-title
                    range
                    scrollable
                    locale="de"
                  >
                    <v-spacer/>
                    <v-btn
                      text
                      color="primary"
                      @click="menu = false"
                    >
                      {{ $t('Cancel') }}
                    </v-btn>
                    <v-btn
                      text
                      color="primary"
                      @click="$refs.menu.save(dates)"
                    >
                      {{ $t('OK') }}
                    </v-btn>
                  </v-date-picker>
                </v-menu>
              </template>
              <template v-slot:footer>
                <v-toolbar
                  color="white"
                  flat
                >
                  <v-spacer/>
                  <v-btn
                    class="mb-2 mr-2"
                    color="primary"
                    dark
                    @click="printByDishCode"
                  >
                    {{ $t('按菜号打印') }}
                  </v-btn>
                  <v-btn
                    class="mb-2"
                    color="primary"
                    dark
                    @click="print"
                  >
                    {{ $t('打印') }}
                  </v-btn>
                </v-toolbar>
              </template>
            </ik-data-table>
          </v-tab-item>
          <v-tab-item>
            <base-material-card
              class="px-5 py-3 "
              color="indigo"
              icon="mdi-vuetify"
              inline
            >
              <template v-slot:after-heading>
                <div class="display-2 font-weight-light">
                  {{ $t('销量实时统计') }}
                </div>
              </template>
              <v-row>
                <v-col cols="2">
                  <v-dialog
                    ref="dialog"
                    v-model="datePickerDialog"
                    :return-value.sync="realTimeDates"
                    persistent
                    width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="realTimeDates"
                        :label="$t('日期筛选')"
                        prepend-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      />
                    </template>
                    <v-date-picker
                      v-model="realTimeDates"
                      scrollable
                    >
                      <v-spacer/>
                      <v-btn
                        text
                        color="primary"
                        @click="datePickerDialog = false"
                      >
                        Cancel
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.dialog.save(realTimeDates)"
                      >
                        OK
                      </v-btn>
                    </v-date-picker>
                  </v-dialog>
                </v-col>
                <v-col cols="2">
                  <v-dialog
                    ref="timeDialog"
                    v-model="timeStartPickerDialog"
                    :return-value.sync="hourStart"
                    persistent
                    width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="hourStart"
                        :label="$t('startTime')"
                        prepend-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      />
                    </template>
                    <v-time-picker
                      v-model="hourStart"
                      range
                      scrollable
                    >
                      <v-spacer/>
                      <v-btn
                        text
                        color="primary"
                        @click="timeStartPickerDialog = false"
                      >
                        Cancel
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.timeDialog.save(hourStart)"
                      >
                        OK
                      </v-btn>
                    </v-time-picker>
                  </v-dialog>
                </v-col>
                <v-col cols="2">
                  <v-dialog
                    ref="timeEndDialog"
                    v-model="timeEndPickerDialog"
                    :return-value.sync="hourEnd"
                    persistent
                    width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="hourEnd"
                        :label="$t('endTime')"
                        prepend-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      />
                    </template>
                    <v-time-picker
                      v-model="hourEnd"
                      range
                      scrollable
                    >
                      <v-spacer/>
                      <v-btn
                        text
                        color="primary"
                        @click="timeEndPickerDialog = false"
                      >
                        Cancel
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.timeEndDialog.save(hourEnd)"
                      >
                        OK
                      </v-btn>
                    </v-time-picker>
                  </v-dialog>
                </v-col>
                <v-col cols="2">
                  <v-select
                    v-model="search"
                    :items="selectedCategoryList"
                    item-text="categoryName"
                    item-value="dishesCategoryId"
                    append-icon="mdi-magnify"
                    :label="$t('Search')"
                    style="max-width: 250px;"
                    multiple
                    @change="selectCategory(search)"
                  />
                </v-col>
                <v-col cols="4">
                  <v-btn
                    x-large
                    color="primary"
                    block
                    @click="refreshData"
                  >
                    {{ $t('刷新数据') }}
                  </v-btn>
                </v-col>
              </v-row>
              <v-divider class="mt-3"/>
              <v-data-table
                :headers="headers"
                :items="showData"
                group-by="timeDuration"
                :items-per-page="100"
                group-desc
              >
                <template v-slot:group.header="items">
                  <td colspan="100%">
                    <div> {{ $t('time') }} :{{ items.group }} {{ $t('hour') }}</div>
                  </td>
                </template>
                <template v-slot:footer>
                  <v-toolbar
                    color="white"
                    flat
                  >
                    <v-spacer/>
                    <v-btn
                      class="mb-2"
                      color="primary"
                      dark
                      @click="printRealTimeSalesBon(search)"
                    >
                      {{ $t('打印') }}
                    </v-btn>
                  </v-toolbar>
                </template>
              </v-data-table>
            </base-material-card>
          </v-tab-item>
        </v-tabs>
      </v-card>
    </v-container>
  </v-container>
</template>

<script>

  import DishStatistics from '../../model/DishStatistics'

  import Utils from '../../components/Utlis/Utils'
  import {
    getRealTimeSaleData,
    PrinterRealTimeSaleBon,
    PrinterSaleBon,
    PrinterSaleBonByDishCode,
  } from '@/components/Utlis/api'
  import Category from '../../model/Category'
  import { formatDate } from '@/common/common'
  import AttributeGroup from '../../model/AttributeGroup'
  import Attribute from '../../model/Attribute'
  import { IkDataTable } from 'metaflow-js'
  import IKUtils from 'innerken-js-utils'

  export default {
    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        tabs: [
          this.$i18n.t('菜品统计'),
          this.$i18n.t('销量实时统计')],
        model: DishStatistics,
        dates: [],
        menu: false,
        dishCategoryTypeIds: [],
        search: [],
        fixedHideCategories: [],
        fixedHideAttributes: [],
        headers: [
          {
            text: this.$i18n.t('name'),
            align: 'start',
            value: 'name',
            groupable: false,
          },
          { text: this.$i18n.t('labelName'), value: 'categoryName', align: 'center', groupable: false },
          { text: this.$i18n.t('count'), value: 'count', align: 'center', groupable: false },
          { text: this.$i18n.t('sumPrice'), value: 'sumPrice', align: 'center', groupable: false },
          { text: this.$i18n.t('timeDuration'), value: 'timeDuration', align: 'center' },
        ],
        resData: [],
        showData: [],
        categoryList: [],
        selectedCategoryList: [],
        dishCategoryIds: [],
        datePickerDialog: null,
        timeStartPickerDialog: null,
        timeEndPickerDialog: null,
        hourStart: '',
        hourEnd: '',
        realTimeDates: '',
        printCategoryIds: [],
        attributeData: [],
        hideAttributeIds: [],
        realAttributes: [],
      })
    },
    computed: {
      filter () {
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        return { start: this.dates[0], end: this.dates[1] }
      },
    },
    watch: {
      dates: function (val) {
      },
    },
    mounted () {
      this.reloadData()
    },
    methods: {
      async refreshData () {
        const timeSpan = [this.realTimeDates + ' ' + this.hourStart, this.realTimeDates + ' ' + this.hourEnd]
        await this.getData(timeSpan)
      },
      selectCategory (search) {
        this.dishCategoryIds = search
        this.showData = this.resData.filter(item => (search.includes(item.dishesCategoryId)))
        if (search.length === 0) {
          this.showData = this.resData
        }
      },
      clearFixedCategory () {
        this.fixedHideCategories = []
        localStorage.removeItem('fixedHideCategories')
      },
      clearFixedHideAttribute () {
        localStorage.removeItem('fixedHideAttribute')
      },
      selectFixedCategory (fixedHideCategories) {
        const fixedHideDishGroups = JSON.stringify(fixedHideCategories)
        localStorage.setItem('fixedHideCategories', fixedHideDishGroups)
        this.selectedCategoryList = this.selectedCategoryList.filter(item => !(fixedHideCategories.includes(item.dishesCategoryId)))
        this.showData = this.resData.filter(item => (fixedHideCategories.includes(item.dishesCategoryId)))
        if (fixedHideCategories.length === 0) {
          this.showData = this.resData
        }
      },
      selectAttributes (attributeId) {
        const attributeIds = JSON.stringify(attributeId)
        localStorage.setItem('fixedHideAttribute', attributeIds)
      },
      groupElement (childList, childMatchKey) {
        const on = function (parentList, matchKey) {
          const _parentList = IKUtils.deepCopy(parentList)
          const saveIn = function (resultKey) {
            childList.forEach(c => {
              const target = _parentList.find(p => c[childMatchKey] === p[matchKey])
              if (target) {
                if (!target[resultKey]) {
                  target[resultKey] = []
                }
                target[resultKey].push(c)
              }
            })
            return _parentList
          }
          return { saveIn }
        }
        return { on }
      },
      async getAllAttribute () {
        return (await Attribute.getList())
          .filter(a => a.optattributeGroupId.length > 0)
      },
      async reloadData () {
        this.realTimeDates = formatDate(0)
        this.hourStart = this.hourStart === '' ? '00:00:00' : this.hourStart
        this.hourEnd = this.hourEnd === '' ? '23:59:59' : this.hourEnd
        this.categoryList = await Category.getList()
        const timeSpan = [this.realTimeDates + ' ' + this.hourStart, this.realTimeDates + ' ' + this.hourEnd]
        await this.getData(timeSpan)
        if (localStorage.getItem('fixedHideAttribute')) {
          this.fixedHideAttributes = JSON.parse(localStorage.getItem('fixedHideAttribute'))
        }
        this.attributeData = await this.getAttributeGrouped()
        this.realAttributes = this.attributeData.reduce((arr, a) => {
          a.childAttributes.forEach(c => arr.push(c))
          return arr
        }, [])
      },
      clear () {
        this.dates = []
      },
      async getData (timeSpan) {
        let orgData = []
        await getRealTimeSaleData(timeSpan).then(res => {
          orgData = res.content.filter(item => item.count > 0)
          if (localStorage.getItem('fixedHideCategories')) {
            this.fixedHideCategories = JSON.parse(localStorage.getItem('fixedHideCategories'))
            this.resData = orgData.filter(item => !(this.fixedHideCategories.includes(parseInt(item.dishesCategoryId))))
          } else {
            this.resData = IKUtils.deepCopy(orgData)
          }
          this.showData = this.resData
        })
        this.showData.forEach(item => {
          let group = this.selectedCategoryList.find(a => a.dishesCategoryId === item.dishesCategoryId)
          if (!group) {
            group = {
              dishesCategoryId: item.dishesCategoryId,
              categoryName: item.categoryName,
            }
            this.selectedCategoryList.push(group)
          }
        })
      },
      async getAttributeGrouped () {
        const attributeGroup = await AttributeGroup.getList()
        const attribute = await this.getAllAttribute()
        return this.groupElement(attribute, 'attributeGroupId')
          .on(attributeGroup, 'id').saveIn('childAttributes')
      },
      printRealTimeSalesBon (search) {
        this.dishCategoryIds = search.join(',')
        const timeSpan = [this.realTimeDates + ' ' + this.hourStart, this.realTimeDates + ' ' + this.hourEnd]
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie Umsatz Bon drucken?', () => {
          PrinterRealTimeSaleBon(timeSpan, this.dishCategoryIds).then(() => {
            Utils.toast('Erfolgreich drucken!')
          })
        })
      },
      printByDishCode () {
        if (this.$refs.statisticTable.filterItem.categoryTypeId) {
          this.dishCategoryTypeIds = this.$refs.statisticTable.filterItem.categoryTypeId
        }
        if (this.$refs.statisticTable.filterItem.categoryId) {
          this.printCategoryIds = this.$refs.statisticTable.filterItem.categoryId
        } else {
          const b = []
          if (localStorage.getItem('fixedHideCategories')) {
            this.fixedHideCategories = JSON.parse(localStorage.getItem('fixedHideCategories'))
            let arr = []
            arr = this.categoryList.filter(item => !(this.fixedHideCategories.includes(parseInt(item.id))))
            arr.forEach(item => {
              b.push(item.id)
            })
          }
          this.printCategoryIds = b.join(',')
        }
        const attrArr = []
        if (localStorage.getItem('fixedHideAttribute')) {
          this.fixedHideAttributes = JSON.parse(localStorage.getItem('fixedHideAttribute'))
          let arr = []
          arr = this.realAttributes.filter(item => !(this.fixedHideAttributes.includes(parseInt(item.id))))
          arr.forEach(item => {
            attrArr.push(item.id)
          })
        }
        this.hideAttributeIds = attrArr.join(',')
        if (this.dates.length === 0) {
          Utils.showError('Bitte wählen Sie ein Startdatum und Enddatum')
        } else {
          Utils.showConfirm('Bist du sicher?', 'Möchten Sie Umsatz Bon drucken?', () => {
            PrinterSaleBonByDishCode(this.dishCategoryTypeIds, this.printCategoryIds, this.hideAttributeIds, this.filter).then(() => {
              Utils.toast('Erfolgreich drucken!')
            })
          })
        }
      },
      print () {
        if (this.$refs.statisticTable.filterItem.categoryTypeId) {
          this.dishCategoryTypeIds = this.$refs.statisticTable.filterItem.categoryTypeId
        }
        if (this.$refs.statisticTable.filterItem.categoryId) {
          this.printCategoryIds = this.$refs.statisticTable.filterItem.categoryId
        } else {
          const b = []
          if (localStorage.getItem('fixedHideCategories')) {
            this.fixedHideCategories = JSON.parse(localStorage.getItem('fixedHideCategories'))
            let arr = []
            arr = this.categoryList.filter(item => !(this.fixedHideCategories.includes(parseInt(item.id))))
            arr.forEach(item => {
              b.push(item.id)
            })
          }
          this.printCategoryIds = b.join(',')
        }
        const attrArr = []
        if (localStorage.getItem('fixedHideAttribute')) {
          this.fixedHideAttributes = JSON.parse(localStorage.getItem('fixedHideAttribute'))
          let arr = []
          arr = this.realAttributes.filter(item => !(this.fixedHideAttributes.includes(parseInt(item.id))))
          arr.forEach(item => {
            attrArr.push(item.id)
          })
        }
        this.hideAttributeIds = attrArr.join(',')
        if (this.dates.length === 0) {
          Utils.showError('Bitte wählen Sie ein Startdatum und Enddatum')
        } else {
          Utils.showConfirm('Bist du sicher?', 'Möchten Sie Umsatz Bon drucken?', () => {
            PrinterSaleBon(this.dishCategoryTypeIds, this.printCategoryIds, this.hideAttributeIds, this.filter).then(() => {
              Utils.toast('Erfolgreich drucken!')
            })
          })
        }
      },
    },
  }
</script>
